    font-family: "Iran_Yekan";
    src: url(./fonts/IRANYekanBoldFaNum.ttf);
}
body {
    background-color: #1A1A1A;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    font-size: 100%;
    text-transform: uppercase;
}